import FocusTrap  from 'react-focus-trap'
import React      from 'react'

export default React.createClass({

  render() {
    return (
      <FocusTrap element="nav" role="navigation" className="col-menu" onExit={ this.props.onExit }>
        { this.props.children }
      </FocusTrap>
    )
  }

})



